.about {
  h6{
    font-weight: 500;
    font-size: 14px;
    border-radius: 50px;
    color: #fff;
    padding: 8px 18px;
    margin-bottom: 10px;
    display: inline-block;
    background: #0652DD;
    text-transform: capitalize;
  }
  h2{
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    margin: 0;
    color: #171718;
    line-height: 1.42;
  }
  height: 100vh;
  padding: 100px 0;
  .mainPhoto{
    transform: rotate(90deg);
    img{

    }
  }
  .aboutInner {
    > p {
      margin-bottom: 30px;
      line-height: 1.9;
      color: #6D7077;
    }
    h2{
      margin-bottom: 20px;
      font-size: 36px;
    }
    .aboutInfo{
      list-style: none;
      margin-bottom: 38px;
      padding: 0;
      li {
        margin-bottom: 15px;
        h5 {
          font-size: 18px;
          font-weight: bold;
          font-family: "Poppins", sans-serif;
        }
        span{
          font-size: 16px;
          font-family: "Roboto", sans-serif;
          font-weight: 400;
          color: #6D7077;
        }
      }

    }
  }


}